@-webkit-keyframes rgb {
  0% {
    -webkit-box-shadow: 0px 0px 30px red;
            box-shadow: 0px 0px 30px red;
  }
  50% {
    -webkit-box-shadow: 0px 0px 70px green;
            box-shadow: 0px 0px 70px green;
  }
  100% {
    -webkit-box-shadow: 0px 0px 30px blue;
            box-shadow: 0px 0px 30px blue;
  }
}

@keyframes rgb {
  0% {
    -webkit-box-shadow: 0px 0px 30px red;
            box-shadow: 0px 0px 30px red;
  }
  50% {
    -webkit-box-shadow: 0px 0px 70px green;
            box-shadow: 0px 0px 70px green;
  }
  100% {
    -webkit-box-shadow: 0px 0px 30px blue;
            box-shadow: 0px 0px 30px blue;
  }
}

.contador-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0px auto;
  background-color: black;
  width: 250px;
  height: 250px;
  text-align: center;
  border: 5px solid #61DAFB;
  margin-top: 50px;
  margin-bottom: 20px;
  -webkit-animation: rgb 3s linear infinite;
          animation: rgb 3s linear infinite;
}

.numero-contador {
  color: white;
  font-size: 70px;
}
