@font-face {
  font-family: "WebSymbolsRegular";
  src: url("./fonts/websymbols-regular-webfont.eot");
  src: url("./fonts/websymbols-regular-webfont.eot?#iefix") format('embedded-opentype'),
      url("./fonts/websymbols-regular-webfont.woff") format('woff'),
      url('./fonts/websymbols-regular-webfont.ttf') format('truetype'),
      url('./fonts/websymbols-regular-webfont.svg#WebSymbolsRegular') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon{
  font-family: "WebSymbolsRegular";
}

@keyframes girar {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  background-color: black;
  width: 100%;
}

nav .reactLogo-container{

  flex-basis: 30%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

nav .reactLogo-container h2{
  color: white;
  text-align: center;
  font-size: 25px;
  margin-left: 10px;
  margin-right: 10px;
}

.reactLogo{
  width: 80px;

  animation: girar 4s linear infinite;
}

.crisRmrz-logo{
  width: 250px;
  margin-top: 10px;
  margin-bottom: 10px;

}

ul{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  flex-basis: 30%;
}

ul li{
  margin-left: 10px;
  margin-right: 10px;
  font-size: 20px;
  font-weight: bolder;
}

ul li a{
  color: white;
  text-decoration: none;
}

.crisRmrz-logo-contenedor{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  flex-basis: 30%;
}

footer{
  background-color: black;
  width: 100%;
  height: 80px;

  display: flex;
  justify-content: center;
  align-items: center;
}

footer h2{
  margin: 0px auto;
  color: white;
  font-size: 25px;
  text-align: center;
}

/*INICIO DE REDES*/

.redes-h1{
  color: white;
  background-color: black;
  border: 5px solid white;
  padding: 20px;
  border-radius: 20px;
  text-align: center;
}

.ul-redes{
 list-style: none;
 margin-top: 20px;
 margin-bottom: 20px;
}

.ul-redes li a{
  font-size: 50px;
  color: #61DAFB;
}

@media (max-width: 1000px) {
  
  nav{
    flex-direction: column;
  }

  .crisRmrz-logo-contenedor{
    margin-top: 20px;
  }

  .reactLogo-container{
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .ul-nav{
    margin-bottom: 30px;
  }

}

@media (max-width: 550px) {
  
  .crisRmrz-logo{
    width: 200px;
  }

  .reactLogo{
    width: 50px;
  }

  .reactLogo-container h2{
    font-size: 22px !important;
  }

}

@media (max-width: 420px) {
  
  .ul-nav li a{
    font-size: 15px;
  }

  .reactLogo{
    width: 40px;
    padding: 10px;
  }

  .reactLogo-container h2{
    font-size: 18px !important;
    padding: 0px;
  }

  .contador-container{
    margin-top: 10px !important;
    width: 150px !important;
    height: 150px !important;
  }

  .boton-click{
    width: 150px !important;
    font-size: 25px !important;
  }

  .boton-reiniciar{
    width: 150px !important;
    font-size: 25px !important;
  }

  .redes-container{
    margin-left: 10px;
    margin-right: 10px;
  }

  .redes-h1{
    font-size: 20px;
  }

  .icon{
    font-size: 40px !important;
  }

}