.contenedor-principal {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-top: 20px;
  margin-bottom: 20px;
}

.boton-click {
  color: white;
  width: 300px;
  height: 80px;
  font-size: 40px;
  background-color: black;
  margin-top: 20px;
  margin-bottom: 10px;
  border: 3px solid #61DAFB;
  border-radius: 100px;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px 5px #61DAFB;
          box-shadow: 1px 1px 5px #61DAFB;
}

