@keyframes rgb {
    0%{
        box-shadow: 0px 0px 30px red;
    }
    50%{
        box-shadow: 0px 0px 70px green;
    }
    100%{
        box-shadow: 0px 0px 30px blue;
    }
}

.contador-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0px auto;
    background-color: black;
    width: 250px;
    height: 250px;
    text-align: center;
    border: 5px solid #61DAFB;
    margin-top: 50px;
    margin-bottom: 20px;

    animation: rgb 3s linear infinite;
}

.numero-contador{
    color: white;
    font-size: 70px;
}